.about {
  width: 100%;
  padding: 60px 20px;
  background-color: #0F0F0F;
  scroll-snap-align: start;
}

.about-container {
  max-width: 1200px;
  margin: 0 auto;
}

.about .section-title {
  font-size: clamp(20px, 4.5vw, 36px);
  color: #8BC34A;
  text-align: center;
  margin-bottom: 50px;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 3px 3px 0px rgba(0, 0, 0, 0.5), 0 0 20px rgba(139, 195, 74, 0.3);
  word-break: break-word;
  overflow-wrap: break-word;
  line-height: 1.4;
}

.about-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  animation: fadeInUp 0.8s ease-out;
  min-width: 0;
}

.about.visible .about-content {
  animation: fadeInUp 0.8s ease-out forwards;
}

.about-text {
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 0;
}

.introduction {
  font-size: clamp(16px, 3.2vw, 18px);
  color: #FF0000;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 1px;
  margin: 0;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.5);
  word-break: break-word;
  overflow-wrap: break-word;
  line-height: 1.6;
}

.bio {
  font-size: clamp(14px, 2.6vw, 16px);
  color: #CCCCCC;
  line-height: 1.8;
  font-family: 'Courier Prime', monospace;
  margin: 0;
  white-space: pre-wrap;
}

.skills-container {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.skills-title {
  font-size: 24px;
  color: #8BC34A;
  margin-bottom: 25px;
  font-family: 'Press Start 2P', cursive;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.5);
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 12px;
}

.skill-badge {
  background: linear-gradient(135deg, #FF0000, #CC0000);
  color: #FFFFFF;
  padding: 15px 12px;
  border-radius: 6px;
  border: 2px solid #FFFFFF;
  font-size: clamp(11px, 2.2vw, 13px);
  text-align: center;
  font-weight: bold;
  font-family: 'Courier Prime', monospace;
  text-transform: uppercase;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(255, 0, 0, 0.2);
  cursor: default;
}

.skill-badge:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(255, 0, 0, 0.6);
  background: linear-gradient(135deg, #FF3333, #FF0000);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1024px) {
  .about-content {
    grid-template-columns: 1fr;
    gap: 30px;
  }

  .skills-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .about {
    padding: 40px 15px;
  }

  .about .section-title {
    font-size: 24px;
    letter-spacing: 1px;
  }

  .introduction {
    font-size: 16px;
    word-break: break-word;
    overflow-wrap: break-word;
    letter-spacing: 0.5px;
    overflow-wrap: break-word;
  }

  .bio {
    font-size: 15px;
    line-height: 1.8;
    word-break: break-word;
    overflow-wrap: break-word;
  }

  .skills-title {
    font-size: 18px;
    margin-bottom: 15px;
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
  }

  .skill-badge {
    padding: 14px 10px;
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .about {
    padding: 30px 12px;
    overflow-x: hidden;
  }

  .about .section-title {
    font-size: clamp(18px, 6vw, 22px);
    letter-spacing: 1px;
    padding: 0 8px;
  }

  .about-content {
    grid-template-columns: 1fr;
    gap: 25px;
  }

  .about-text {
    gap: 15px;
    min-width: 0;
  }

  .introduction {
    font-size: clamp(16px, 4.5vw, 18px);
    word-break: break-word;
    overflow-wrap: anywhere;
    hyphens: auto;
    letter-spacing: 0.25px;
  }

  .bio {
    font-size: clamp(14px, 4vw, 16px);
    line-height: 1.8;
    word-break: break-word;
    overflow-wrap: anywhere;
    hyphens: auto;
    white-space: pre-wrap;
  }

  .skills-container {
    width: 100%;
  }

  .skills-title {
    font-size: 16px;
    margin-bottom: 12px;
  }

  .skills-grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;
  }

  .skill-badge {
    padding: 14px 10px;
    font-size: 13px;
    line-height: 1.3;
  }

  .skill-badge:hover {
    transform: scale(1.03);
  }
}
